@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";
* {
    box-sizing: border-box;
}
fieldset {
    padding: 0;
}
 body {
    background-color: #292929;
    color: #ffffff;
    font-family: "GothamPro", sans-serif;
 }
 a {
     cursor: pointer;
     color: #ffffff;
 }
 button {
    cursor: pointer;
 }

 .header {
    position: relative;
    padding-top: 54px;
    &__wrapper {
        @include row-flex();
        }
    &__logo {
        @include col();
        @include size(2);
        @include size-lg(3);
        @include size-sm(12);
    }
    &__info {
        margin:0;
        font-size: 12px;
        line-height: 1.2;
        font-weight: 300;
        letter-spacing: 0.01em;
        text-align: left;
        @include col();
        @include size(2);
        @include size-lg(5);
        @include size-md(4);
        @include size-sm(12);
        span {
            font-weight: 500;
            color: #ff3469;
        }
    }
    &__contacts {
        text-align: right;
        @include col();
        @include size(8);
        @include size-lg(4);
        @include size-md(5);
        @include size-sm(12);
    }
    &__number {
        font-size: 20px;
        line-height: 1.2;
        font-weight: 500;
        letter-spacing: 0.01em;  
        text-decoration: none;
    }
    &__call {
        margin-top: 7px;
        font-size: 12px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;  
    }
    &__link {
        display: inline-block;
        position: relative;
        font-size: 16px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;
        color: #ff3469;
        margin-top: 16px;
        min-width: 135px;
        &:hover:before {
            position: absolute;
            content: '';
            bottom: 0;
            width: 100%;
            height: 1px;
            background-color: #ff2e55;
            transition: all ease-in-out .5s;
        }
    }
    &:before {
        position: absolute;
        content: "";
        top: 0;
        left: 65vw;
        background: url('../img/promo_fl_1.png') no-repeat;
        width: 39px;
        height: 24px;
    }
}
    @include sm-block() {
        .header {
            &__logo {
                text-align: center;
            }
            &__info {
                margin-top: 16px;
                text-align: center;
                font-size: 14px;
            }
            &__contacts {
                margin-top: 16px;
                text-align: center;
            }
        }
    } 


 .promo {
     text-align: center;
     padding: 123px 0 77px 0;
     &:before {
        position: absolute;
        content: "";
        top: 140px;
        left: 10%;
        background: url('../img/promo_fl_2.png') no-repeat;
        width: 93px;
        height: 153px;
    }
    &:after {
        position: absolute;
        content: "";
        top: 409px;
        right: 10%;
        background: url('../img/promo_fl_3.png') no-repeat;
        width: 71px;
        height: 61px;
    }
     &__title {
         margin: 0 auto;
         max-width: 692px;
        font-size: 34px;
        line-height: 1.2;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 0.01em;
     }
     &__subtitle {
        margin: 0 auto;
        max-width: 562px;
        font-size: 18px;
        line-height: 1.2;
        font-weight: 300;
        text-transform: uppercase;
        letter-spacing: 0.01em;
        margin-top: 33px;
         span {
            font-weight: 500;
         }
     }
     &__btn {
         margin-top: 36px;
        width: 300px;
        height: 70px;
        background-color: #ff2e55;
        border: 3px solid #ff2e55;
        font-size: 18px;
        line-height: 1.2;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 0.01em;
        color: #ffffff;
     }
     &__discount {
         margin-top: 85px;
         font-size: 19px;
        line-height: 1.2;
        font-weight: 400;
        text-transform: uppercase;
        letter-spacing: 0.01em;
     }
 }
 
@include xl-block() {
    .promo {
        &:before {
            top: 110px;
            left: 5%;
        }
    }
} 
@include md-block() {
    .promo {
        padding-top: 100px;
        &:before {
            transform: rotate(-70deg);
        }
        &:after {
            top: 380px;
        }
        &__title {
            font-size: 26px;
        }
        &__subtitle {
            font-size: 16px;
        }
    }
} 
@include sm-block() {
    .promo {
        &:before {
            transform: rotate(0deg);
            top: 180px;
        }
        &:after {
            top: 100px;
            right: 0;
        }
    }
} 
@include xs-block() {
    .promo {
        &:before {
            display: none;
        }
        &:after {
            top: 270px;
        }
    }
} 
 .flowers-slider {
     margin-top: 100px;
     width: 100%;
 }
 .flowers-slider .swiper-button-next:after, .flowers-slider .swiper-button-prev:after {
    font-size: 18px;
    color: gray;
 }
 .flowers-slider .swiper-button-next {
     right: 0;
 }
 .flowers-slider .swiper-button-prev {
    left: 0;
}
.flowers-slider .swiper-button-next:hover:after, .flowers-slider .swiper-button-prev:hover:after {
    color: #ff2e55;
 }
 .advantages {
     text-align: center;
     padding-bottom: 101px;
     border-top: 1px solid #4c4c4c;
    &__title {
        margin-top: 100px;
        font-size: 30px;
        line-height: 1.2;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 0.01em;
     }
     &__wrapper{
         margin-top: 98px;
     @include row-flex();
     flex-wrap: wrap;
     justify-content: space-around;
     }
     &__item {
         min-height: 180px;
         display: flex;
         flex-direction: column;
         align-items: center;
     }
     &__subtitle {
         margin-top: 30px;
         font-size: 20px;
        line-height: 25px;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 0.01em;
        color: #ffffff;
        margin-top: auto;
     }
 }
 .advantages-slider {
     margin-top: 96px;
 }
 
 .review {
     text-align: center;
     display: flex;
     align-items: center;
     justify-content: center;
    &__photo {
        position: relative;
        flex-shrink: 0;
        width: 220px;
        margin-left: 85px;
        margin-right: 52px;
    }
    &__name{
        font-size: 20px;
        line-height: 1.2;
        font-weight: 500;
        letter-spacing: 0.01em;
        position: absolute;
        width: 100%;
        left: 50%;
        top: 40%;
        transform: translateX(-50%);
    }
    &__city {
        font-size: 18px;
        font-weight: 400;
        letter-spacing: 0.01em;
        position: absolute;
        width: 100%;
        left: 50%;
        top: 50%;
        transform: translateX(-50%);
    }
     &__text {
        text-align: left;
        max-width: 50%;
        margin-top: 29px;
        font-size: 18px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;
     }
     &__link {
         margin-top: 19px;
         display: block;
        text-align: left;
        font-size: 18px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;
        color: #f24841;
     }

 }
 .advantages-slider .swiper-scrollbar {
     display: none;
 }
 .advantages-slider .swiper-button-next {
    background: url(../img/arrow_lg_right.svg) no-repeat;
    right: 0;
 }
 .advantages-slider .swiper-button-prev {
    background: url(../img/arrow_lg_left.svg) no-repeat;
    left: 0;
 }
 .advantages-slider .swiper-button-prev:after, .advantages-slider .swiper-button-next:after {
    content: '';
 }
 :root {
    --swiper-navigation-size: 36px;
 }
 @include md-block() {
    .advantages {
        position: relative;
        .advantages-slider {
            margin-top: 76px;
        }
        .advantages-slider .swiper-button-prev,  .advantages-slider .swiper-button-next{
            display: none; 
        }
        .advantages-slider .swiper-scrollbar {
            position: static;
            display: block;
            overflow: auto;
            top: 0;
            background: rgba(163, 59, 59, 0.582);
        }
        &__item:nth-child(3) {
            margin-top: 25px;
        }
    }
    .review {
            display: block;
       &__photo {
           margin: 22px auto;
       }
       &__text {
        margin: 0 auto;
        text-align: center;
        max-width: 100%;
        margin-top: 18px;
     }
     &__link {
        text-align: center;
    }
    }

}
 .bouquets {
    border-top: 1px solid #4c4c4c;
     padding-bottom: 69px;
     text-align: center;
     &__title{
    margin-top: 93px;
     text-align: center;
     font-size: 30px;
    line-height: 1.2;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.01em;
    span {
        color: #ff1844;
    }
     }
     &__subtitle {
        font-size: 18px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;
        margin-top: 27px;
     }
     &__wrapper {
         margin-top: 50px;
        @include row-flex();
        justify-content: space-between;
     }
     &__link {
        display: inline-block;
        margin-top: 39px;
        font-size: 14px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;
        color: #f24841;

        &:after {
            display: inline-block;
            margin-left: 10px;
            width: 15px;
            height: 10px;
            content: "";
            background: url(../img/arrow_down.svg) no-repeat 0 0;
        }
     }
     .card {      
        margin-top: 27px;
        padding: 33px 35px 30px 35px;
        text-align: center;
        min-height: 375px;
        border: 3px solid #ffffff;
        @include col();
        @include size(3);
        @include size-lg(4);
        @include size-md(6);
        @include size-sm(12);
        &__name {
            margin-top: 23px;
            font-size: 20px;
            line-height: 1.2;
            font-weight: 500;
            text-transform: uppercase;
            letter-spacing: 0.01em;
        }
        &__label {
            display: block;
            margin-top: 20px;
            font-size: 14px;
            line-height: 1.2;
            font-weight: 400;
            letter-spacing: 0.01em;
        }
        &__btn {
            margin-top: 20px;
            width: 140px;
            height: 40px;
            background-color: #f24841;
            font-size: 14px;
            line-height: 1.2;
            font-weight: 500;
            text-transform: uppercase;
            letter-spacing: 0.01em;
            color: #ffffff;
        }
     }
 }
 .colors {
     border:none;
     margin-top: 7px;
     display: flex;
     align-items: center;
     justify-content: space-between;
     &__input {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        z-index: -9;
     }
     &__label {
        width: 20px;
        height: 20px;
        background-color: #ffffff;
        cursor: pointer;
     }
     &__label.biege {
        background-color: #f7b496;
     }
     &__label.pink {
        background-color: #ed366e;
     }
     &__label.red {
        background-color: #ff0000;
     }
     &__label.blue {
        background-color: #12129d;
     }

 }
 .flowerscount {
    margin-top: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
 .counter-input {
    width: 40px;
    height: 40px;
    border: 2px solid #ffffff;
    text-align: center;
    color: #ffffff;
    background-color:  transparent;
 }
 .counter {
    width: 40px;
    height: 40px;
    text-align: center;
    color: #ffffff;
    background-color:  transparent;
    border: none;
    font-size: 16px;
    line-height: 1.2;
    font-weight: 500;
    letter-spacing: 0.01em;
}
@include lg-block() {
    .bouquets {
        &__wrapper {
           justify-content: center;
        }
    }
}
@include md-block() {
    .bouquets {
        &__wrapper {
           justify-content: space-around;
        }
    .card {
        max-width: 300px;
    }
    }
}

 .order {
    border-top: 1px solid #4c4c4c;
     padding: 100px 0 84px 0;
    background-color: #1a1a1a;
    line-height: 1.2;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.01em;
    text-align: center;
    transition: all ease-in-out .5s;
    &__text{
        font-size: 30px;
    }
    &__title{
        position: relative;
        display: inline-block;
        margin-top: 29px;
        font-size: 30px;
        &:before {
            position: absolute;
            content: '';
            left: 0%;
            bottom: 0;
            width: 100%;
            height: 1px;
            background-color: #ffffff;
            
        }
    }
    &__callback {
        margin-top: 65px;
        font-size: 14px;
        font-weight: 400;
    }
    &__number {
        display: block;
        margin-top: 22px;
        font-size: 36px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;    
        text-decoration: none;
    }
 }
 @include md-block() {
    .order {
        &__title{
            &:before {
                bottom: -10px;
                
            }
        }
    }
}
 .footer {
    &__wrapper {
        @include row-flex();
        flex-wrap: wrap;
        justify-content: space-between;
        }
    &__links {
        list-style-type: none;
        display: flex;
    }
     &__contacts {
        padding: 48px 0 44px 0;
        border-top: 1px solid #4c4c4c;
        background-color: #292929;
     }
     .link__delivery, .link__payment {
        font-size: 18px;
        line-height: 1.2;
        font-weight: 500;
        letter-spacing: 0.01em;
    }
    .link__delivery {
        margin-right: 35px;
    }
    &__media {
        margin-top: 33px;
        font-size: 16px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;
        display: flex;
    }
    .link__vk {
        margin-left: 20px;
    }
     &__callback {
         text-align: right;
        font-size: 14px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;
     }
     &__number {
        display: block;
        font-size: 20px;
        line-height: 1.2;
        font-weight: 500;
        letter-spacing: 0.01em;
        text-decoration: none;
        margin-top: 12px;
        margin-bottom: 11px;
    }
     &__info {
        padding: 48px 0 44px 0;
        padding-bottom: 44px;
        /* min-height: 132px; */
        background-color: #ffffff;
     }
     &__logo {
        @include col();
        @include size(2);
        @include size-lg(3);
        @include size-md(4);
        @include size-xs(12);

    }
    &__descr {
        margin:0;
        font-size: 12px;
        line-height: 1.2;
        font-weight: 300;
        letter-spacing: 0.01em;
        color: #292929;
        @include col();
        max-width: 125px;
        @include size-lg(6);
        @include size-md(4);
        span {
            font-weight: 500;
            color: #ff3469;
        }
    }
    &__dev {
        font-family: "MyriadPro";
        font-size: 12px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.01em;
        color: #101010;
        @include col();
        @include size(8);
        @include size-lg(3);
        @include size-md(4);
        text-align: right;
        margin: auto 0 auto 0;
    }
 }
 @include sm-block() {
    .footer {
        &__wrapper {
            display: block;   
        }
        &__wrapper2 {
            display: flex;  
            justify-content: space-between; 
        }
        &__media {
            margin: 0; 
        }
        &__callback {
            margin-top: 25px;
            text-align: center;
        }
        &__logo {
            display: block;
           margin: 0 auto;
        }
        &__descr {
            display: block;
            margin: 16px auto;
            text-align: center;
            font-size: 14px;
            min-width: 300px;
        }
        &__dev {
       text-align: center;
       margin: 16px auto;
        }
    }
}
@include xs-block() {
    .order__number{
    font-size: 29px;
    }
    .footer {
        &__wrapper2 {
            display: block;
        }
        &__links {
            justify-content: center;
        }
        &__media {
            margin: 20px; 
            text-align: center;
        }
        &__callback {
            margin-top: 25px;
            font-size: 18px;
            text-align: center;
        }
        &__logo {
           text-align: center;
        }
        &__dev {
            font-size: 15px;
        }
    }
}
.modal {
    display:none;
    
    &__title {
        color: #070505;
        font-size: 20px;
        font-weight: 500;
        text-align: center;
    }
    &__input{
        display: block;
        margin: 20px auto;
        width: 300px;
        height: 40px;
    }
    &__btn {
    margin-top: 36px;
    width: 300px;
    height: 70px;
    background-color: #ff2e55;
    border: 3px solid #ff2e55;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.01em;
    color: #ffffff;
    }
}